body{
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  display: grid;
}

.App {
  font-size: 15px;
  
}

.App .title {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  background-color: #0078d4;
  color: white;
  padding-left: 1rem;
  width: 100%;
  margin-bottom: 0.5rem;
  font-size: 0.8rem;
}

.App .topicFilterHeader {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 1rem 3rem 0rem 3rem;
  column-gap: 10rem 2rem;
}

.search{
  display: flex;
  justify-content: center;
  margin: 1rem 1rem 1rem 1rem;
}

.search .box{
  width: 25%;
}


.buttons{
  margin: 1rem 1rem 1rem 1rem;
}

.button{
  margin: 10rem;
}

.items{
  display: grid;
  grid-gap: 16px;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  text-align: center;
  margin: 10px 10px 10px 10px;
}


.footer {
  display: flex;
  flex-direction: row;
  width: 100vw;
  padding-right: 0.5em;
  padding-left: 0.5em;
  justify-content: left;
  background-color: #001B2E;
  color: white;
  font-size: 1rem;
}

.footer a{
  color: white;
  font-size: 0.8rem;
}
